                active_types.includes('type2') ? '' : 'inactive'
                ]"
              @click="toggleEventTypeActive('type2')"
              ></div>
              <div
                :class="[
                'event-color', event_colors['type3'],
                active_types.includes('type3') ? '' : 'inactive'
                ]"
                @click="toggleEventTypeActive('type3')"
              ></div>
              <div
                :class="[
                'event-color', event_colors['type4'],
                active_types.includes('type4') ? '' : 'inactive'
                ]"
                @click="toggleEventTypeActive('type4')"
              ></div>
            </div>
          </v-toolbar>
          <v-list
            color="skej-grey-background"
            flat
            avatar
            disabled
            single-line
            >
            <v-list-item class="mb-4">
              <v-card flat tile
              color="skej-grey-background"
              class="border-blue mr-4"
              >
                <v-card-text
                  class="font-weight-medium">
                  All Day
                </v-card-text>
              </v-card>
              <v-card flat tile color="skej-grey-background">
                <v-card-text>Birthday</v-card-text>
              </v-card>
            </v-list-item>
            <div  v-for="hour in hours" :key="`hour-${hour.hr}`">
              <v-list-item v-if="hour.hr >= day_start">
                <v-card flat tile
                  color="skej-grey-background ml-4">
                  <v-card-text
                    class="font-weight-bold py-0">
                    <div :class="[
                      'hrstart',
                      'pr-4',
                      hour.start ? `border-${event_colors[hour.type]}`:''
                    ]">
                      {{hour.hr}}:00
                    </div>
                    <div
                    :class="['hr15', 'text--disabled', hour.quarter ? `border-${event_colors[hour.type]}`:'']"
                    >
                      15
                    </div>
                    <div
                    :class="['hr30', 'text--disabled', hour.half ? `border-${event_colors[hour.type]}`:'']"
                    >
                      30
                    </div>
                    <div
                    :class="['hr45', 'text--disabled', hour.quarter_til ? `border-${event_colors[hour.type]}`:'']"
                    >
                      45
                    </div>
                  </v-card-text>
                </v-card>
                <v-card flat tile v-if="hour.label_hour"
                  color="skej-grey-background">
                  <v-card-text class="py-0 event-name">
                    <div :style="hour.label_quarter=='start'?'opacity: 1':'opacity: 0'">
                        <span v-show="active_types.includes(hour.type)">{{hour.event_name}}</span>
                    </div>
                    <div :style="hour.label_quarter=='quarter'?'opacity: 1':'opacity: 0'">
                        <span v-show="active_types.includes(hour.type)">{{hour.event_name}}</span>
                    </div>
                    <div :style="hour.label_quarter=='half'?'opacity: 1':'opacity: 0'">
                        <span v-show="active_types.includes(hour.type)">{{hour.event_name}}</span>
                    </div>
                    <div :style="hour.label_quarter=='quarter_til'?'opacity: 1':'opacity: 0'">
                        <span v-show="active_types.includes(hour.type)">{{hour.event_name}}</span>
                    </div>
                  </v-card-text>
                </v-card>
              </v-list-item>
            </div>
          </v-list>
        </div>
      </v-col>
      <v-col cols="12" sm="6" lg="4">
        <div class="todolist scroll-section">
          <v-toolbar
