<script>
var hours = []
var tasks = []
var testData = {}
for (var i = 1; i < 25; i++) {
  hours.push({
    hr: i,
    start: false,
    quarter: false,
    half: false,
    quarter_til: false,
    event_name: '',
    event_color: '',
    label_quarter: false,
    label_hour: 0
  })
}

var events = [
  {
    name: 'Breakfast',
    hours: [
      { hr: 8, start: true, quarter: true, half: true, quarter_til: true }
    ],
    hours_list: [8],
    label_hour: 8,
    label_quarter: 'quarter',
    // color: 'border-pink',
    type: 'type1'
  },
  {
    name: 'Shopping',
    hours: [
      { hr: 13, start: true, quarter: true, half: true, quarter_til: true },
      { hr: 14, start: true, quarter: true, half: true, quarter_til: true },
      { hr: 15, start: true, quarter: true, half: true, quarter_til: false }
    ],
    hours_list: [13, 14, 15],
    label_hour: 14,
    label_quarter: 'quarter',
    // color: 'border-green'
    type: 'type2'
  },
  {
    name: 'Dinner',
    hours: [
      { hr: 19, start: true, quarter: true, half: true, quarter_til: true },
      { hr: 20, start: true, quarter: true, half: true, quarter_til: true }
    ],
    hours_list: [19, 20],
    label_hour: 19,
    label_quarter: 'quarter_til',
    // color: 'border-blue'
    type: 'type3'
  },
  {
    name: 'Concert',
    hours: [
      { hr: 22, start: true, quarter: true, half: true, quarter_til: true },
      { hr: 23, start: true, quarter: true, half: true, quarter_til: true },
      { hr: 24, start: true, quarter: true, half: true, quarter_til: true }
    ],
    hours_list: [22, 23, 24],
    label_hour: 23,
    label_quarter: 'quarter',
    // color: 'border-blue'
    type: 'type4'
  }
]

for (var hour of hours) {
  for (var event of events) {
    if (event.hours_list.includes(hour.hr)) {
      let eventHour = null
      for (var ehr of event.hours) {
        if (ehr.hr === hour.hr) {
          eventHour = ehr
        }
      }
      hour.start = eventHour.start
      hour.quarter = eventHour.quarter
      hour.half = eventHour.half
      hour.quarter_til = eventHour.quarter_til
      hour.type = event.type
      hour.label_quarter = event.label_quarter
      hour.event_name = event.name
      if (event.label_hour === hour.hr) {
        hour.label_hour = true
      }
    }
  }
}

for (var j = 0; j < 20; j++) {
  var types = ['type1', 'type2', 'type3', 'type4']
  var index = Math.floor(Math.random() * Math.floor(4))
  tasks.push({
    name: `Task ${j + 1}`,
    type: types[index]
  })
}

testData.hours = hours
testData.tasks = tasks

export default {
  name: 'yourDay',
  data: () => ({
    active_types: ['type1', 'type2', 'type3', 'type4'],
    event_colors: { type1: 'cyan', type2: 'green', type3: 'orange', type4: 'red' },
    hours: testData.hours,
    day_start: 7,
    tasks: testData.tasks,
    today: new Date().toLocaleDateString('en-US', { year: 'numeric', month: 'long', day: 'numeric' })
  }),
  methods: {
    toggleEventTypeActive (type) {
      if (this.active_types.includes(type)) {
        var index = this.active_types.indexOf(type)
        this.active_types.splice(index, 1)
      } else {
        this.active_types.push(type)
      }
    }
  }
}
</script>
